<template>
<div class="start">
  <div class="slogan">
    Antique Fu - {{$route.from}}
  </div>
  <div v-bind:class="className" id = "allInOne">
    <input type="text" class="searchColumnCommon" id="searchBar"v-model="search" @input="checkSearch" @keypress="handleEnter">
    <button type="button" name="button" id = "searchBtn"class="searchColumnCommon" @click="handleSearch" v-html="searchBtnIcon"></button>
  </div>
  <div class="result">
    <!-- Blog -->
    <blog :list = "list" :search="search"></blog>
    <!-- Blog End-->
    <!-- Help -->
    <div class="ani" v-if="search=='help'">
      availible command : [blog] [nav]
    </div>
    <!-- Help End -->
    <!-- Nav -->
    <navmain :navInfo="navInfo" v-if="search=='nav'"></navmain>
    <!-- <div class="nav" v-if="search=='nav'">
      <button type="button" :class="modifyClass" name="button" @click="handleModify" v-html="modifyIcon"></button>
      <div class="newNav" v-if="modify">
        <input type="text" class="newNavInput" placeholder="column" v-model="newColumn">
        <input type="text" class="newNavInput" placeholder="name" v-model="newSiteName">
        <input type="text" class="newNavInput" placeholder="addr" v-model="newSiteAddr">
        <button type="button" name="button" @click="handleNewNav" id="addNew">+</button>
      </div>
      <navcolumn v-for="(column,key) in navInfo" :column="key" :content="column" :navInfo="navInfo" :modify="modify" v-if="JSON.stringify(column)!=='{}'"></navcolumn>
    </div> -->
  </div>
</div>
</template>

<script>
 var icon={
   search:"<i class='fa-search fa fa-fw'>",
   setting:"<i class='fa fa-cog fa-fw'></i>",
   settingSpin:"<i class='fa fa-cog fa-fw fa-spin'></i>",
   blog:"<i class='fa fa-book fa-fw '></i>",
   help:"<i class='fa fa-question fa-fw '></i>",
   nav:"<i class='fa fa-paper-plane fa-fw '></i>",
 }
 // console.log(this.$watch);
 import blog from './Blog.vue'
 import navmain from './NavMain.vue'
 var getFromPost = ''
 var initState ="state1"
 export default {
  name: 'hello',
  props: ['list','from','navInfo','info'],
  components:{blog:blog,navmain:navmain},
  data() {
    return {
      result: "",
      search: this.from||getFromPost||'nav',
      className: initState,
      hoverClass:'blogcolumn',
      hovering:true,
      searchBtnIcon:icon.search,
      focusData:['nav','blog','help']
    }
  },
  methods: {
    checkSearch: function() {
      var t = this.search;
      var keywds = ['blog','help','nav'];
      var check = 0
      for (let w of keywds) {
        if (t===w) {
          check=t
          break
        }
      }
      if (check) {
        this.className='state2'
        switch (check) {
          case 'blog':
            this.searchBtnIcon=icon.blog
            break;
          case 'nav':
            this.searchBtnIcon=icon.nav
            break;
          case 'help':
           this.searchBtnIcon=icon.help
          default:
          break

        }
      }else {
        this.className='state1'
        this.searchBtnIcon=icon.search
      }
    },
    handleSearch: function(e,kw=this.search) {
      // preventDefault()
      // var t = keyWords||this.search
      if (kw.indexOf('www.') >= 0 || kw.indexOf('.com') >= 0) {
        if (!kw.indexOf('://')>=0) {
          window.open('http://'+kw)
        }
      }else{
        window.open('http://www.baidu.com/baidu?wd='+kw)
      }
    },
    handleEnter:function(e){
      if (this.search&&e.key==='Enter') {
          this.handleSearch(null,this.search)
      }
    }
  },
  beforeRouteEnter(to,from,next){
    if (from.name==='post') {
      getFromPost='blog'
      initState="state2"
    }
    next()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style scoped>
.start {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
}
.state1{
  transition: all 1s;
  margin: 0 auto;
  margin-top: 250px;
  margin-bottom: 20px;
}


@media screen and (max-width: 1100px) {
  /*.newNav{display: none;}*/
  .nav{width: 100%;}
}
.state2{
  transition: all 1s;
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 150px;
}
.searchColumnCommon{
  float: left;
  background: transparent;
  background-color: rgba(255, 255, 255, 0.55);
  border: none;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  height: 2rem
}
#searchBar{
  border-right: 2px solid #FFFFFF!important;
  text-align: center;
  width: 30rem;
  font-size: 1rem

}
#searchBtn{
  width: 2rem;
  text-align: center;
  border-top-right-radius: 50%;
  border-bottom-right-radius: 50%;
  font-size: 1rem;
}

.slogan{
  width: 80%;
  margin-left: 10%;
  margin-top: 20px;
  font-size: 3rem;
  color: #FFFFFF
}
.result{
  border-radius: 15px;
  display: flex;
  /*flex-direction: column;*/
  justify-content: center;
  width: 80%;
  /*margin: 0 auto;*/
  margin-left: 10%;
}

</style>



