<template lang="html">
  <div class="nav">
    <button type="button" :class="modifyClass" name="button" @click="handleModify" v-html="modifyIcon"></button>
    <div class="newNav" v-if="modify">
      <input type="text" class="newNavInput" placeholder="column" v-model="newColumn">
      <input type="text" class="newNavInput" placeholder="name" v-model="newSiteName">
      <input type="text" class="newNavInput" placeholder="addr" v-model="newSiteAddr">
      <button type="button" name="button" @click="handleNewNav" id="addNew">+</button>
    </div>
    <navcolumn v-for="(column,key) in navInfo" :column="key" :content="column" :navInfo="navInfo" :modify="modify" v-if="JSON.stringify(column)!=='{}'"></navcolumn>
  </div>
</template>

<script>
import navcolumn from './NavColumn.vue'

var icon={
  search:"<i class='fa-search fa fa-fw'>",
  setting:"<i class='fa fa-cog fa-fw'></i>",
  settingSpin:"<i class='fa fa-cog fa-fw fa-spin'></i>",
  blog:"<i class='fa fa-book fa-fw '></i>",
  help:"<i class='fa fa-question fa-fw '></i>",
  nav:"<i class='fa fa-paper-plane fa-fw '></i>",
}
export default {
  props:['navInfo'],
  components:{navcolumn:navcolumn},
  data:function(){
    return {
      modifyClass:"modify",
      modifyIcon:icon.setting,
      modify:false,
      newColumn:'',
      newSiteName:'',
      newSiteAddr:'',
    }
  },
  methods:  {
    handleModify:function(e){
      this.modify=!this.modify
      if (this.modify) {
        this.modifyClass="modify endModify"
        this.modifyIcon=icon.settingSpin
      }else {
        this.modifyClass="modify"
        this.modifyIcon=icon.setting
        var ls =window.localStorage
        ls.navInfo = JSON.stringify(this.navInfo)
      }
    },
    handleNewNav:function(e){
      //TODO 向已存在的column中添加site会导致无法立即更新dom
      if (!(this.newColumn&&this.newSiteName&&this.newSiteAddr)) {
        console.log('error');
        return
      }else {
        var newSite={
          name:this.newSiteName,
          addr:this.newSiteAddr
        }
        try {
          var oldColumn = this.navInfo[this.newColumn]

          if (!oldColumn) {
            this.$set(this.navInfo,this.newColumn,[newSite])
          } else {
            //找重复
            var haveold = false
            oldColumn.forEach((site,i)=>{
              if (site&&site.name===this.newSiteName) {
                oldColumn[i]=newSite
                this.$set(this.navInfo,this.newColumn,oldColumn)
                haveold=true
              }else {
                // console.log('del null');
                if (!site) {
                  oldColumn.splice(i,1);
                }

              }
            })
            // console.log('pass2');
            if (!haveold) {
              oldColumn.push(newSite)
              this.$set(this.navInfo,this.newColumn,oldColumn)
            }
          }
        } catch (e) {
          console.log(e);
        }
        var ls =window.localStorage
        ls.navInfo = JSON.stringify(this.navInfo)
      }
    },
  }
}
</script>

<style lang="css">
.endModify{
  background: #FF5400!important;
  border-radius: 5px!important;
  transform: rotate(135deg);
}
.modify{
  transition: all 0.5s;
  background: #0AC433;
  position: absolute;
  left: 0.5rem;
  top:1.5rem;
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  border: none;
  text-align: center;
  padding: 0;
  outline: none;
  margin: 0 auto;
  z-index: 2;
}
.modify:hover{
  width: 2rem;
  height: 2rem;
  background: #F99543;
}
.nav{
  position: relative;
  padding-top: 3rem;
  width: 40%
}
.newNav{
  position: absolute;
  top:1.5rem;
  left: 10%;
  width: 90%;
  min-height: 2rem;
  max-height: 3rem;
}
.newNavInput{
  height: 2rem;
  margin: 0;
  border: 0;
  border-radius: 0.5rem;
  text-align: center;
  width: 30%;
}
#addNew{
  max-width: 10%;
  min-width: 2rem;
  height: 2rem;
  border-radius: 0.5rem;
  background: #FFF;
  border: none;
  padding: 0;
  margin: 0;
}
</style>



