<template lang="html">
  <div class="post">
    <div class="title">
      <span class="left">{{post.title}} </span><span class="left"> {{post.modifiedTime}}</span>
      <router-link to='/' class = "back">
         <mu-raised-button label="goback"/>
      </router-link>
    </div>
    <div v-html ="post.content" class="content">
    </div>
  </div>
  <!-- <mu-card>
    <mu-card-header :title="post.title" :subTitle="post.modifiedTime">
    </mu-card-header>
    <mu-card-text v-html="post.content">
    </mu-card-text>
    <mu-card-actions>
      <router-link to='/' class = "back">
         <mu-raised-button label="go back"/>
      </router-link>
    </mu-card-actions>
  </mu-card> -->
</template>

<script>
export default {
  name: 'post',
  data:function(){
    try {
      var post = require('./posts/convert/'+this.$route.params.id+'.json');
    } catch (e) {
      console.log(e);
      var post ={
        title:"404",
        content:"Post Not Found"
      }
    }
    return {
      post:post
    }
  }
}

</script>

<style>
  .post{
    /*background: #ccc;*/
    background-color: rgba(168, 168, 168, 0.36);
    width: 70%;
    position: absolute;
    top: 0%;
    left: 15%;
    bottom: 0%;
  }
  .left{
    margin-right: 1rem;
  }
  .title{
    text-transform: capitalize;
    font-size: 2.5rem;
    position: relative;
    text-align: left;
    text-indent: 2rem;
    padding: 1rem 0;
    box-shadow: 2px 2px 10px;
  }
  .mu-card{
    margin: 2%;
    height: 90%;
    padding: 0;
    border: none;
    box-sizing: border-box;
    background: transparent;
    background-color: rgba(167, 167, 167, 0.61);
    box-shadow: 12px 12px 25px
  }
  .back{
    height: 2.5rem;
    float: right;
    text-decoration: none;
    color: #FFF;
    margin: 0;
    padding: 0;
    border: none;
    text-indent: 0;
    background: #6E6E6E;
    font-size: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center!important;
  }
  .content{
    text-decoration: none;
    background: #CCC;
    padding: 0;
    margin: 0;
    padding: 1rem;
  }

</style>



