import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Post from '@/components/Post'
Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello,
    },
    {
      path: '/post/:column/:id',
      name: 'post',
      component:Post,
    }
  ]
})



