<template lang="html">
  <div class="navcolumn" v-if="!empty">
    <div class="columnName">{{column}}</div>
    <div class="columnContent" v-for="(site,key) in content" v-if="site&&site.addr&&site.name">
      <button :value="'http://'+site.addr" class="navLink" v-if="!modify" @click="handleNav">{{site.name}}</button>
      <div class="modifyDiv" v-if="modify">
        <div v-if="modify" class="modifyDetail">
          <i class="fa fa-close" @click="del(window.event,column,key)"></i>
          &nbsp;&nbsp;{{site.name}}&nbsp;&nbsp;
          <i class="fa fa-pencil"></i></div>
      </div>
    </div>
    <div class="cf"></div>
  </div>
</template>

<script>
export default {
  props:["column","content","modify","navInfo"],
  data:function(){
    var empty = true
    this.navInfo[this.column].forEach(c=>{
      if (!!c) {
        empty=false
      };
    });
    return {
      window:window,
      empty:empty
    }
  },
  methods:{
    handleNav:function(e){
      window.open(e.target.value)
    },
    handleInput:function(e){
      var name = e.path[1].children[0].value
      var addr = e.path[1].children[1].value
    },
    del:function(e,c,k){
      // console.log(e.target);
      this.navInfo[c].splice(k,1)
      console.log(this.navInfo[c]);
      // delete this.navInfo[c][k]
      // e.target.parentNode.style.display='none'
      // console.log(arguments);
      // console.log(event);
      // console.log('delete');
      // console.log(this.navInfo[c][a]);
    },
    handleModify:function(e){
      console.log('modify');
    },
    endModify:function(e){
      console.log('endModify');
    },
  },
}
</script>

<style lang="css" scoped>
.cf{
  clear: both;
}
.navcolumn{
  align-items: center;
  /*flex-direction: column;*/
  justify-content: flex-start;
  flex-wrap: wrap;
  margin: 10px;
}
.navLink{
  display: inline-block;
  margin: 10px;
  color: #FFF;
  padding: 10px;
  text-decoration: none;
  border: 1px solid #FFF;
  float: left;
  border-radius: 10px;
  background: transparent;
  outline: none;
  min-width: 6rem;
}
.navLink:hover{
  transition: all 0.2s;
  background: rgba(179, 179, 179, 0.52);
  transform: scale(1.1);
}
.columnName{
  text-align: left;
  padding-left: 20px;
  width: 100%;
  background:transparent;
  background-color: rgba(199, 199, 199, 0.24);
  font-size: 1.2rem;
  font-family: weiruanyahei;
  text-transform: capitalize;
  padding: 5px;
}
.columnContent{
  display: inline-block;
  float: left;
}
.modifyDiv{
  float: left;
  margin: 10px;
  width: 100%;
}
.modifyDetail{
  margin: 10px;
  display: flex;
  border: 1px solid #FFF;
  border-radius: 0.5rem;
  padding: 0.2rem;
  align-items: center;
  justify-content: center;
}
.modifyDetail:hover{
  margin: 9px;
  border: 2px solid #FFF;
  border-style: dashed;
}
input{background:transparent;
  height: 1rem;
  color: #FFF;
  font-family: weiruanyahei;
  border: 1px solid #FFF;
  border-radius: 5px;
  text-indent: 1rem;
  padding: 0.5rem;
}
.modifyInput{
  height: 2rem;
}
</style>



