<template lang="html">
  <div  v-if="search=='blog'" class ="columnContainer" @mouseover="handleHover">
    <div :class="classList[key]" v-for="(name,key) in columnList">
      <i :class="iconList[key]"></i>
      <div class="" v-if="act==key" v-for="post in list[name]">
        <router-link :to="'/post/'+name+'/'+post.title">{{post.title}} : {{post.modifiedTime}}</router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['list', 'search'],
  data: function() {
    return {
      threod:false,
      act:0,
      columnList:['listen','balabala','read','markdown'],
      iconList:["fa fa-music","fa fa-microphone","fa fa-book","fa fa-pencil"],
      classList:["blogcolumn active","blogcolumn deactive","blogcolumn deactive","blogcolumn deactive"]
    }
  },
  methods: {
    //鼠标悬浮时 目标栏增大
    //其余栏 隐藏
    handleHover: function(e) {
      if (e.target.className.indexOf("blogcolumn")>=0) {
        var father = e.path[1]
        var that = this
        Array.from(father.children).forEach((t,i)=>{
          if (father.children[i]===e.target) {
            that.act = i
          }
          t.className="blogcolumn deactive"
        })
        e.target.className="blogcolumn active"
      }
    },
  }
}
</script>

<style lang="css" scoped>
a{
  text-decoration: none;
  color:#FFF;
  text-align: center;
}
.columnContainer{
  flex-direction: column;
  text-indent: 2rem;
  width: 500px;
  margin: 0 auto;
  height: 500px;
  display: flex;
}
.blogcolumn{
  border: 1px solid #B3E9FC;
  box-sizing: border-box;
  display: flex;
  padding-top: 2rem;
  justify-content: flex-start;
  transition: all 0.6s;
}
.active{
  background-color: rgb(250, 153, 51);
  flex-grow: 10;
}
.deactive{
  background: transparent;
  background-color: rgba(250, 153, 51, 0.23);
  flex-grow: 1;
}

#columnA{
  /*border-top-left-radius: 125px;*/
  background: #FFCE70
}
/*#columnA:hover,#columnB:hover,#columnC:hover,#columnD:hover{
  transition: all 1s;
  background: #55AFAF;
  width: 100%;
}*/
#columnB{
  /*border-top-right-radius: 125px;*/
  background: #F6BB49
}
#columnC{
  /*border-bottom-left-radius: 125px;*/
  background: #FBB226
}
#columnD{
  /*border-bottom-right-radius: 125px;*/
  background: #FFA800
}


</style>



